* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
html {
  font-size: 62.5%;
}
a {
  color: unset;
  text-decoration: none;
}
.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}
.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  background:var(--color-bg)
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }
}

@media screen and (max-width: 380px) {
  .section__padding {
    padding: 4rem 1.5rem;
  }

  .section__margin {
    margin: 4rem 1.5rem;
  }
}

::-webkit-scrollbar-thumb:hover {
  background: #c4ae836e;
}

::-webkit-scrollbar-thumb {
  background: #c4ae83;
  border-radius: 5px;
}

::-webkit-scrollbar {
  background: #14171a;
  max-width: 10px;
  height: 10px;
}
